@include ipad-landscape {
	
	#tribe-events-content-wrapper {
		
		#tribe-events-content {
			
			&.tribe-events-list {
				padding: 0;
			}
			
			.tribe-events-page-title {
				padding: 0;
			}
			
			.tribe-events-sub-nav {
				
				li {
					
					&.tribe-events-nav-previous,
					&.tribe-events-nav-next {
						position: relative;
						top: 0;
					}
					
					a {
						background: none;
						padding: 10px 0 20px;
					}
				}
			}
			
			table.tribe-events-calendar {
				
				tbody {
					
					td {
						height: 70px;
						
						.type-tribe_events {
							padding: 0;
						}
						
						.tribe-events-month-event-title {
							font-size: 14px;
							line-height: 22px;
						}
						
						div[id*=tribe-events-daynum-] {
							font-size: 14px;
							line-height: 22px;
						}
					}
				}
				
				.tribe-events-has-events:after {
					position: relative;
					top: 10px;
				}
			}
			
			#tribe-events-footer {
				
				&~a {
					
					&.tribe-events-ical {
						
						&.tribe-events-button {
							height: auto;
						}
					}
				}
			}
		}
		
		#tribe-mobile-container {
			
			.type-tribe_events {
				
				&~.type-tribe_events {
					border-top: 1px solid $default-border-color;
				}
				
				h4 {
					text-transform: none;
				}
				
				.tribe-events-read-more {
					font-family: $default-heading-font-m;
					font-size: 12px;
					line-height: 24px;
					font-weight: 700;
					text-transform: uppercase;
				}
			}
		}
	}
}

@include phone-landscape {
	
	#tribe-events-content-wrapper {
		
		#tribe-bar-form {
			
			#tribe-bar-collapse-toggle {
				width: 100%;
			}
			
			.tribe-bar-filters {
				margin-bottom: 26px;
				
				.tribe-bar-filters-inner {
					
					> div {
						width: 100% !important;
					}
					
					.tribe-bar-date-filter,
					.tribe-bar-search-filter {
						padding-right: 0;
					}
				}
				
				.tribe-bar-submit .tribe-events-button {
					margin: 20px 0 30px;
				}
			}
			
			#tribe-bar-views {
				margin: 0 0 27px;
				
				.tribe-bar-views-inner {
					
					> label {
						margin: 0 14px 0 0;
					}
				}
			}
		}
		
		#tribe-events-content {
			
			.tribe-events-page-title {
				font-size: 22px;
			}
			
			table.tribe-events-calendar {
				
				thead {
					
					th {
						font-size: 12px;
						line-height: 24px;
						padding: 7px 2px;
					}
				}
				
				tbody {
					
					td {
						
						div[id*=tribe-events-daynum-] {
							font-size: 14px;
						}
					}
				}
			}
			
			&.tribe-events-list {
				
				.eltdf-events-single-meta-item {
					font-size: 13px;
					margin-top: 20px;
				}
			}
		}
		
		#tribe-mobile-container {
			
			.tribe-events-event-schedule-details {
				display: block;
				margin: 0 0 10px;
			}
		}
	}
}